    decodeEntities: function decodeEntities(str) {
      // Decode Html Values inside an array to pass to the arguments
      try {
        if (str && typeof str === 'string') {
          str = str.replace(/<script[^>]*>([\S\s]*?)<\/script>/gmi, '');
          str = str.replace(/<\/?\w(?:[^"'>]|"[^"]*"|'[^']*')*>/gmi, '');
        }
        return str;
      } catch (e) {
        var errorStats = { 'status': 415, 'ErrorTryCatchMsg': e, 'ErrorMsgFromPage': 'Object doesnt support this action -> Decode Html Values inside an array to pass to the arguments' };
        var functionName = 'Connect:decodeEntities';
        _staff2.default.checkError(errorStats, functionName);
      }
    },
