    checkError: function checkError(res, local) {
        // Error reciever
        var errorMsg = '';
        var userInfo = this.data.auth.user;
        var IEbrowser = _browser_detect2.default.detectIE();
        var browser_select = '';

        browser_select = IEbrowser.trigger ? { 'browser': IEbrowser.browser, 'version': IEbrowser.version } : { 'browser': _browser_detect2.default.specs().browser.name, 'version': _browser_detect2.default.specs().browser.version };

        for (var e in _error_logs2.default.error) {
            if (_error_logs2.default.error[e].code == res.status) {
                errorMsg = {
                    'userAuth': userInfo.authenticated,
                    'userRole': userInfo.role,
                    'userEmail': userInfo.profile.email,
                    'userFname': userInfo.profile.first_name,
                    'userLname': userInfo.profile.last_name,
                    'statusPageMsg': res.ErrorMsgFromPage,
                    'statusMsg': res.ErrorTryCatchMsg ? res.ErrorTryCatchMsg.message : _error_logs2.default.error[e].msg,
                    'statusCode': _error_logs2.default.error[e].code,
                    'statusText': _error_logs2.default.error[e].status,
                    'specsOS': _browser_detect2.default.specs().os.name,
                    'specsOSversion': _browser_detect2.default.specs().os.version,
                    'specsBrowser': browser_select.browser,
                    'specsBrowserversion': browser_select.version,
                    'location': local
                };
            }
        }
        this.updateCheck({ "": "" }, errorMsg);
    },
